@import url('https://fonts.googleapis.com/css?family=Monoton');

@font-face {
    font-family: ItalianBreakfast;
    src: url(./multimedia/ItalianBreakfast-Regular.otf) format("opentype");
}

/* Header e footer */
header {
    position: relative;
    background-image: url('logo.jpg') ;
    height: 470px;
    margin-bottom: 20px;
    border-bottom: 1px solid black;
}

header p {
    position: relative;
    top: 5px;
    padding: 30px;
}

#titolo { 
    animation-name: blinked; 
    animation-duration: 2s; 
    animation-iteration-count: infinite;
} 

#lamp {
    animation: upper 11s linear infinite;
}

#lamp2 {
    animation: lower 10s linear infinite;
}

.block {
    height: 100%;
}

.block {
    margin: 0;
    padding: 0;
    background-color: #454545;
    background-image: linear-gradient(45deg, transparent, transparent 48%, rgba(255, 255, 255, .3) 48%, rgba(255, 255, 255, .3) 52%, transparent 52%);
    background-size: 10px 10px;
    position: absolute;
    width: 100%;
    font-size: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 327px;
    margin-top: -20px;
}

.block:after {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    background-image: radial-gradient(circle, transparent 0%, rgba(7, 9, 11, 0.56) 100%);
}

.render {
    display: inline-block;
    z-index: 0;
    position: relative;
    line-height: 0.55em;
    height: 0.7em;
    -moz-transform: rotateY(45deg);
    -webkit-transform: rotateY(45deg);
    transform: rotateY(45deg);
    font-family: 'Bree Serif', serif;
    font-size: 4em;
    color: whitesmoke;
}

footer {
    padding: 10px;
    height: 280px;
    background-image: url('race-car.gif');
    background-position: center;
    margin-top: -11px;
    border-top: 1px solid black;
}

footer>a {
    position: relative;
    left: 40px;
    text-shadow: white 0.006em 0.006em 1px, rgba(0, 0, 0, 0.15) -3px 7px 10px, #d0d0d0 -1px 1px 1px, #d0d0d0 -2px 1px 1px, #d0d0d0 -3px 1px 1px, #d0d0d0 -4px 1px 1px, #d0d0d0 -5px 1px 1px, #d0d0d0 -6px 1px 1px, #d0d0d0 -7px 1px 1px, #d0d0d0 -8px 1px 1px, #d0d0d0 -9px 1px 1px, #d0d0d0 -10px 1px 1px, #d0d0d0 -11px 1px 1px, #d0d0d0 -12px 1px 1px, rgba(255, 255, 255, 0.6) -5px 2px 100px;
    padding: 35px;
    top: 20px;
}

footer p {
    position: relative;
    left: 40px;
    width: 250px;
    text-shadow: white 0.006em 0.006em 1px, rgba(0, 0, 0, 0.15) -3px 7px 10px, #d0d0d0 -1px 1px 1px, #d0d0d0 -2px 1px 1px, #d0d0d0 -3px 1px 1px, #d0d0d0 -4px 1px 1px, #d0d0d0 -5px 1px 1px, #d0d0d0 -6px 1px 1px, #d0d0d0 -7px 1px 1px, #d0d0d0 -8px 1px 1px, #d0d0d0 -9px 1px 1px, #d0d0d0 -10px 1px 1px, #d0d0d0 -11px 1px 1px, #d0d0d0 -12px 1px 1px, rgba(255, 255, 255, 0.6) -5px 2px 100px;  
}

footer p a {
    text-decoration: none;
    color: black
}

footer p a:hover,
footer p a:focus,
footer p a:active {
    background-color: whitesmoke;
    color: grey;
    text-decoration: overline underline;
}

/* Men√π di Navigazione */
.sidebar {
    height: 100%;
    width: 200px;
    position: fixed;
    z-index: 1;
    top: -16px;
    left: -40px;
    background-image: url('logo.jpg');
    overflow-x: hidden;
    padding-top: 60px;
    animation: trasla1 0.5s 1;
    background-position: left;
}

.sidebar li {
    padding: 8px 8px 8px 32px;
    font-size: 13px;
    text-decoration: none;
    display: block;
    transition: 0.3s;
    color: black;
    text-shadow: white 0.006em 0.006em 1px, rgba(0, 0, 0, 0.15) -3px 7px 10px, #d0d0d0 -1px 1px 1px, #d0d0d0 -2px 1px 1px, #d0d0d0 -3px 1px 1px, #d0d0d0 -4px 1px 1px, #d0d0d0 -5px 1px 1px, #d0d0d0 -6px 1px 1px, #d0d0d0 -7px 1px 1px, #d0d0d0 -8px 1px 1px, #d0d0d0 -9px 1px 1px, #d0d0d0 -10px 1px 1px, #d0d0d0 -11px 1px 1px, #d0d0d0 -12px 1px 1px, rgba(255, 255, 255, 0.6) -5px 2px 100px;
}

.sidebar li a {
    text-decoration: none;
    color: black;
    position: relative;
    bottom: 5px;
}

.sidebar li img {
    position: relative;
    right: 18px;
}

.sidebar .closebtn {
    position: absolute;
    top: 0;
    right: 25px;
    font-size: 30px;
    margin-left: 50px;
}  

.openbtn {
    bottom: 125px;
    position: relative;
    font-size: 20px;
    cursor: pointer;
    background-color: #111;
    color: white;
    padding: 10px 15px;
    border: none;
}  

.openbtn:hover {
    background-color: #444;
}

#main {
    transition: margin-left .5s;
}

#main1 {
    transition: margin-left .5s;
    margin-left: 201px;

}
#imgf {
    left: 5px;
    top: 10px;
    background: #00000030;
}

/* Animazioni */
@keyframes blinked {
    from { top: 1%; 
    }
    to { top: 4%;
    } 
}

@keyframes upper {
    0%,19.999%,22%,62.999%,64%, 64.999%,70%,100% {
      opacity:.99; text-shadow: 0 0 80px #ffffff,0 0 30px #008000,0 0 6px #0000ff;
    }
    20%,21.999%,63%,63.999%,65%,69.999% {
      opacity:0.4; text-shadow: none; 
    }
}

@keyframes lower {
    0%,12%,18.999%,23%,31.999%,37%,44.999%,46%,49.999%,51%,58.999%,61%,68.999%,71%,85.999%,96%,100% {
      opacity:0.99; text-shadow: 0 0 80px red,0 0 30px FireBrick,0 0 6px DarkRed;
    }
    19%,22.99%,32%,36.999%,45%,45.999%,50%,50.99%,59%,60.999%,69%,70.999%,86%,95.999% { 
      opacity:0.4; text-shadow: none; 
    }
}

@keyframes trasla {
    from {
        position: relative;
        right: 200px;
        opacity: 0.5;
    }
    to {
        right: 0px;
        opacity: 1;
    }
}

@keyframes trasla1 {
    from {
        left: -240px;
        opacity: 0.5;
    }
    to {
        opacity: 1;
    }
}

